/*a-propos*/

.A-Propos-contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 365px;
    
}

.navbar-background {
    width: 90%;
    height: 223px;
    object-fit: cover;
    border-radius: 40px;
}

/*style pour mobile*/

@media screen and (max-width: 425px) {

    .A-Propos-contents {
        margin-bottom: 30%;
    }

    .navbar-background{
        margin-bottom: 25px;
    }

}